/* pretendard */
@font-face {
	font-family: 'Pretendard';
	font-style: normal;
	font-weight: 900;
	font-display: block;
	src: url(./font/pretendard/eot/Pretendard-Black.eot);
	src: url(./font/pretendard/eot/Pretendard-Black.eot?#iefix) format("embedded-opentype"), url(./font/pretendard/woff/Pretendard-Black.woff) format("woff");
}

